<template>
  <div class="notfound__content">
    <div class="notfound__title">提醒訊息：抱歉，頁面不存在！</div><br>
    <div class="notfound__small">系統跳轉中，請稍後...</div>
    <div class="notfound__link">
      【<router-link to="/" replace> 如果不想等待，請點此處跳轉 </router-link>】
    </div>
  </div>
</template>

<script>
import { useRouter } from "vue-router"
import { timeoutPromise } from "../utils";

export default {
  name: "NotFound",
  setup() {
    const router = useRouter()
    timeoutPromise(() => {
      router.replace({ path: '/' })
    }, 3000);
    return {};
  },
};
</script>
